import React from "react";
import styled from "styled-components";
import { Menu } from "@styled-icons/material-sharp/Menu";

import { Button } from "../../0-base/Button";
import { Icon } from "../../0-base/Icon";

const StyledNavigationDrawerButton = styled(Button)``;

const StyledMenuIcon = styled(Icon).attrs({
  as: Menu,
})`
  width: 24px;
  height: 24px;

  color: ${(props) => props.theme.color.white};
`;

export default function NavigationDrawerButton(): JSX.Element {
  return (
    <StyledNavigationDrawerButton>
      <StyledMenuIcon />
    </StyledNavigationDrawerButton>
  );
}
